export const contactsReducer=(state:ContactsStateType=initialState,action:ActionType):ContactsStateType=>{
    switch (action.type) {
        case 'FOLLOW': {
            return {...state,contacts:state.contacts.map(m=> m.id===action.userID ? {...m,followed:true} : m)} //  false  true
        }
        case 'UN-FOLLOW':{
            return {...state,contacts:state.contacts.map(m=> m.id===action.userID  ?{...m,followed:false} : m)}//   true  false
        }
        case 'SET-USERS':{
            return {...state,contacts: action.users} //    ,    (  )
        }
        case  'ACTUAL-PAGE':{
            return {...state,actualPage:action.page}
        }
        case 'USER-TOTAL-COUNT':{
            return {...state,totalUserCount:action.page}
        }

        case 'CHANGE-FETCHING':{
            return {...state,isFetching:action.value}
        }
        default:
            return  state;
    }
}

export const followAC = (userID: string) => {  //     follow -     id  
    return {
        type: 'FOLLOW',
        userID,
    } as const
}

export const unFollowAC = (userID: string) => { //     unfollow -     id  
    return {
        type: 'UN-FOLLOW',
        userID,
    } as const
}
// // c c       =:
export const setUsersAC = (users:Array<ContactsType>) => {
    return {
        type: 'SET-USERS',
        users,
    } as const
}
//  :
export const actualPageAC = (page:number) => {
    return {
        type: 'ACTUAL-PAGE',
        page,
    } as const
}

export const userTotalCountAC=(page:number) => {
    return {
        type: 'USER-TOTAL-COUNT',
        page,
    } as const
}

export const changeFetchingAC=(value:boolean) => {
    return {
        type: 'CHANGE-FETCHING',
        value,
    } as const
}

//Thunk-creator:

export const getUsersThunkCreator=(actualPage: number, pageSize: number)=>(dispatch:Dispatch)=>{
    dispatch( changeFetchingAC(true));//true-    
    getApiUsers(actualPage, pageSize).then(data=> { //get 
        dispatch(changeFetchingAC(false));// - 
        dispatch(setUsersAC(data.items)); //         
        dispatch(userTotalCountAC(data.totalCount));
    });
}
export const onPageChangeThunkCreator=(page: number, pageSize: number)=>(dispatch:Dispatch)=>{
    dispatch(actualPageAC(page));
    dispatch( changeFetchingAC(true));//true-    
    onPageChange(page,pageSize).then(data=> { //get 
        dispatch(changeFetchingAC(false));// - 
        dispatch(setUsersAC(data.items)); //         

    });
}

export const unfollowHandlerThunkCreator=(userID:string)=>(dispatch:Dispatch)=>{
    dispatch(followButtonTrueDisabledAC(userID)) //   disabled
    unfollowApi(userID).then(data=> { //get 
        if(data.resultCode===0){
            dispatch(unFollowAC(userID));
        }
        dispatch(followButtonFalseDisabledAC(userID)) // disabled c   id
    })
}

export const followHandlerThunkCreator=(userID:string)=>(dispatch:Dispatch)=>{
    dispatch(followButtonTrueDisabledAC(userID)) //   disabled
    followApi(userID).then(data=> { //get 
        if(data.resultCode===0){
            dispatch(followAC(userID));
        }
        dispatch(followButtonFalseDisabledAC(userID)) // disabled c   id
    })
}